  text-transform: uppercase;
}

.radio {
  border-radius: 50%;
}

.control__input:checked + .radio::before {
  position: absolute;
  top: 8.28px;
  left: 8.25px;
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
  content: "";
}